<template>
  <h1>원룸 리스트</h1>
  <br>  
  <button @click="stringSort">문자순 정렬</button>
  <button @click="priceSort">적은 숫자순 정렬</button>
  <button @click="priceSort_back">많은 숫자순 정렬</button>
  <button @click="sortBack">정렬값 되돌리기</button>

  <Rooms 
    v-for="(room, i) in roomsList" :key="i" 
    :roomNumber="roomsList[i].productNumber" 
    :roomName="'room'+roomsList[i].productNumber" 
    :roomtitle="roomsList[i].title" 
    :roomImage="roomsList[i].image" 
    :roomContent="roomsList[i].content" 
    :roomPrice="roomsList[i].price" 
    :roomIncrease="roomsList[i].increase"
    @increase="roomsList[i].increase++"
    @popup_open="popup_on(i, $event)"
  />

</template>

<script>
import onerooms from '../../assets/oneroom.js';
import RoomUI from './RoomUI.vue';

export default {
  name: 'roomPage',
  components: {
    Rooms : RoomUI
  },
  data() {
    return { 
      roomsList: onerooms,
      roomsListOrigin: [...onerooms]
    }
  },
  methods: {
    increase (){
      this.increaseCount += 1; // data() 내부의 변수에 접근하려면 this.를 사용해야 하고 로컬변수나 매개변수처럼 Vue 인스턴스와 무관한 데이터는 this 없이 사용해야 합니다.
    },
    stringSort(){ // 문자순 정렬
      this.roomsList.sort(function(a, b){
        return a.title.localeCompare(b.title);
      })
    },
    priceSort(){ // 적은 숫자순 정렬
      this.roomsList.sort(function(a, b){
        return a.price - b.price
      })
    },
    priceSort_back(){ // 많은 숫자순 정렬
      this.roomsList.sort(function(a, b){
        return b.price - a.price
      })
    },
    sortBack(){ // 정렬값 되돌리기
      this.roomsList = [...this.roomsListOrigin];
    }
  }
}
</script>

