import { createWebHistory, createRouter } from "vue-router";
 
// 라우터 사용할 컴포넌트 등록
import Page404 from './components/Page_404.vue';
import roomPage from './components/room/Room.vue';
import detailPage from './components/detail/Detail.vue';
import detailAuthor from './components/detail/Author.vue';
import detailComment from './components/detail/Comment.vue';

// 등록한 컴포넌트 상세입력
// 위로 갈수록 우선권이 있음.
const routes = [    
  {
    path: "/detail/:id",  
    component: detailPage,
    // detail 로 접근하면 상세페이지 보여줌.
    // id는 자유롭게 작명가능. 
    // :id* -> id 이후로 중복된 경우
    // :id(//d+) 이런식으로 정규식도 가능
    children: [{
        path: 'author', 
        component: detailAuthor
        // '/detail/변수/author' 접근됨
        // children 이하는 / 안넣어도 됨
    },{
        path: 'comment',
        component: detailComment  
        // '/detail/변수/comment' 접근됨
    }]
  },{
    path: "/detail",  
    component: detailPage
  },{    
    path: "/room", 
    component: roomPage,
    // room 으로 접근하면 원룸 페이지 보여줌
  },{
    path: "/",
    component: roomPage,
    // 루트로 접근하면 원롬 페이지 보여줌
    /*
    beforeEnter: ()=>{
       if (true){
        return '/login'
       }
    }
    이런식으로 리다이렉팅도 가능 */
  },{
    path: "/:qkdmlqwkdmqlwkdm",
    component: Page404,
    // 루트 이후 아무거나 작성하면 에러 페이지 보여줌(404 같은 에러페이지 만드는 원리)
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 