<template>
  <!-- 유효성검사 메모영역 -->
  <div>
    <!--

    v-model

    <input v-model="month">
    <input v-model="month" type="checkbox"> // 체크유무 true & false 로 반환해줌
    <input v-model="month" type="radio"> //
    <textareat v-model="month"></textarea>
    <select v-model="month"></select>
    
    ...
    
    data() {
      return{
        month: 1 // 초기값
      }
    }    
    // <input @input="$event.target.value"> 의 축약버전
    // 사용자의 입력값을 실시간으로 받음
    // 문자 입력받을거면 초기값을 문자로, 숫자면 숫자로, 형식에 맞게 초기값 세팅을 해줘야 함.
    
    <input v-model.numer="month">
    <input :value="value" @input="value = parseFloat($event.target.value) || 0">
    // 사실 사용자가 input에 입력한것은 전부 문자 자료형이다. 숫자로 받고 싶으면 .number를 써주자
    // parseFloat($event.target.value)를 사용하여 문자열을 숫자로 변환함. || 0는 변환 결과가 NaN인 경우(즉, 숫자로 변환할 수 없는 경우) 0으로 대체하는 역할
    
    *********************************************************************************************************************************
    *********************************************************************************************************************************

    @input=""

    <input @input="$event.target.value">    
    // 사용자의 입력값을 실시간으로 받음
    
    *********************************************************************************************************************************
    *********************************************************************************************************************************

    @change=""
    
    <input @change="">
    입력하고 커서다른데 찍으면 뭐 실행해주셈

    -->
  </div>
  <!-- 유효성검사 메모영역 -->

  <!-- v-if="조건식" 조건이 참일 때만 html 보여줌 -->
  <div class="black-bg">
    <div class="white-bg">
      <img :src="viewimg" alt="">
      <h4>{{ viewName }}</h4>

      <div>
        <input v-model.number="month">
        <p>{{ month }}개월 선택함 {{ viewPrice * month }}</p>
      </div>      

      <div>
        <button @click="plus">인원추가</button>
        <p>{{ person }}</p>
        <button @click="minus">인원감소</button>
      </div>

      <div>
        <input type="range" min="1" max="12" v-model="day">
        <p>{{ day }}</p>
      </div>

      <p>{{ viewCtt }}</p>
      <p>{{ viewContent }}</p>
      <button @click="$emit('popup_close')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name: 'modalPopup',
    data() {
      return {
        month: 1,
        person: 1,
        day: 1
      }
    },
    props: {
        viewimg: String,
        viewName: String,
        viewPrice: Number,
        viewContent: String
    },
    watch: {
      month(a, b){ // month 라는 데이터가 변할때마다 여기있는 코드 실행됨. a는 month에 입력된 데이터임. b는 month에 입력하기 전 데이터임.
        if(isNaN(a) == true){
          alert('문자입력하지마라');
          this.month = 1;
        }
        if (a >= 13){
          alert('13개월 이상은 등록할수없습니다.');
          this.month = b
        }
      }
    },
    methods: {
      plus(){
        if (this.person < 9){
          this.person++
        } else {
          alert('신청 인원은 9명이 최대입니다.')
        }
      },
      minus(){
        if (this.person > 1){
          this.person--;
        } else {          
          alert('신청 인원이 1명 미만일 수 없습니다.');
        }
      }
    }
}
</script>

<style>
  .black-bg {position: fixed;top: 0;left: 0;width: 100%;height: 100%;background: rgba(0, 0, 0, 0.5);position: fixed;padding: 20px;}
  .white-bg {width: 100%;background: white;border-radius: 8px;padding: 20px;}
</style>