<template>
  <!-- 기본지식 메모영역 -->
  <div>
        <!-- 

        *********************************************************************************************************************************
        *********************************************************************************************************************************

        v-bind:속성자
        :속성자

        태그에 바인딩시    
        <h4>{{ rooms[i].roomName }}</h4>
        <img :src="rooms[i].roomVisual">
        <img v-bind:src="rooms[i].roomVisual">
        문자열    : {{}}
        HTML속성  : v-bind:href, :href 
        // ':'는 v-bind:의 단축표현

        컴포넌트에 바인딩시
        <Comment viewCtt="asd" />
        <Comment :viewCtt="111" />
        <Comment :viewCtt="[000,111,222]" />
        <Comment :viewCtt="{a:'111',b:111,c:[0,1,2]}" />
        문자열  : ':'를 사용하지 않고 전달함.
        그외    : 변수나 데이터 속성은 'v-bind:' 혹은 ':'로 바인딩하여 전달함.

        *********************************************************************************************************************************
        *********************************************************************************************************************************
        
        :class 바인딩

        <div :class="{'open': on, 'close': !on}">
        // on 이라는 변수가 true 면 'open', false 면 'close' 가 붙음.
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        v-show
        
        // v-show*를 사용해 현재 탭에 해당하는 콘텐츠만 표시합니다.

        *********************************************************************************************************************************
        *********************************************************************************************************************************

        v-if, v-else
        
        <div v-if="modalpop == 1"></div>
        <div v-else-if="modalpop == 2"></div>
        <div v-else></div>
        // modalpop 이라는 변수가 1일 경우 첫번째, 2일 경우 두번째, 둘다 아닐 경우 3번째를 보여줌

        *********************************************************************************************************************************
        *********************************************************************************************************************************

        v-for
        
        <div v-for="(roomName, i) in rooms2" :key="i">
        // v-for는 반복할때 사용합니다. (roomName, i) 앞에 roomName은 고유의 데이터를 심어주며,
        i는 반복할때마다 0부터 시작하여 1씩 증가하는 변수입니다. key 값은 보통 i 를 넣어주는데 꼭 심어주도록 합시다.

        *********************************************************************************************************************************
        *********************************************************************************************************************************

        v-on:click
        @click

        <button v-on:click="popup_onoff(i)">상세페이지</button>
        <button @click="popup_onoff(i)">상세페이지</button>
        // 자바스크립트의 onClick 과 같은 이벤트 핸들러로 vue에서는 v-on:click을 사용합니다. 다른 핸들러 속성도 마찬가지.
        // @ 은 v-on:의 단축 표현

        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        import CommentArea from './components/comment.vue'
        // 컴포넌트 추가시 스크립트에 심어주고
        
        components: {
          Comment: CommentArea,
        }
        // 하단 스크립트에 선언해주고(논리설명을 위해 왼쪽오른쪽 다른 이름을 썻지만 보통은 모두 다 같은 이름으로 통일시킴.)

        <Comment />
        // html 템플릿 안에서 사용하면 됩니다.
        
        *중요* 하위 컴포넌트에 명시된 name은 단순히 디버깅, 개발자 도구에서의 식별 용도로 사용되며
        부모 컴포넌트에서 import할 때는 name 속성이 아니라 파일의 import된 변수명을 사용합니다.
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        export default {
          name: 'commentBox',
          props: {
            title: String,
            subtitle: String,
            ctt: String,
            number: Number,
            comment: String
          }
        }
        // 하위컴포넌트에 미리 props를 세팅해두면

        <Comment title="안녕하십니까??" subtitle="뷰 처음 써보는 뉴비입니다" ctt="많이 알려주세요"  number="10023132" comment="번 정도??"/>
        // 위와 같이 부모 컴포넌트에서 불러올때 각각의 내용을 수정해서 가져올 수 있습니다.
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        <button @click="$emit('작명')">닫기</button>
        <button @click="$emit('작명', 데이터)">닫기</button>
        <Modal v-if="isModalOpen" @작명="isModalOpen = false" />
        // 기본 문법

        <button @click="$emit('closeModal')">닫기</button>
        // 하위 컴포넌트에서 '$emit'를 활용해 임의의(closeModal) 이벤트를 부모 컴포넌트에게 발신    

        <Modal v-if="isModalOpen" @closeModal="isModalOpen = false" />
        // 부모 컴포넌트는 하위 컴포넌트가 발신한 임의의(closeModal) 이벤트를 수신하고 자바스크립트 명령심어줌
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        정렬기능

        <button @click="priceSort">가격순정렬</button>

        ...

        data() {
          return { 
            roomsList: onerooms,
            roomsListOrigin: [...onerooms], // 요즘은 원본 데이터를 보존한 채로 sort를 사용하기 때문에 [...변수] 문법으로 항상 데이터 보존을 심어주자
          }
        },    
        methods: {
          // 문자순 정렬
          stringSort(){
            this.roomsList.sort()
          },
          // 적은 숫자순 정렬
          priceSort(){
            this.roomsList.sort(function(a, b){ // a와 b는 각 배열 항목(roomsList[a], roomsList[b])을 말하는 것이므로 각 항목의 값(숫자)에 접근하고 싶으면 a.price b.price 등으로 접근하면 됨. 
              return a.price - b.price
            })
          },
          // 많은 숫자순 정렬
          priceSort_back(){ 
            this.roomsList.sort(function(a, b){ // a와 b는 각 배열 항목(roomsList[a], roomsList[b])을 말하는 것이므로 각 항목의 값(숫자)에 접근하고 싶으면 a.price b.price 등으로 접근하면 됨. 
              return b.price - a.price
            })
          },
          // 정렬값 되돌리기
          sortBack(){
            this.roomsList = [...this.roomsListOrigin]; // 원본 데이터 복원 로직
          }
        }
        // sort()는 기본적으로 문자형 가나다순 정렬이다. 위와 같은 코드는 배열안에 객체로 접근하기 때문에 추가 과정이 필요한 것  
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************
        -->
  </div>
  <!-- // 기본지식 메모영역 -->  
  <!-- // 라이프사이클 메모영역 -->
  <div>
        <!--
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        <Banner v-if="showBanner == true" />

        mounted() {
          this.showBanner = false;
        },
        data() {
          return { 
            showBanner: true,
          }
        },

        라이프사이클 -> 컴포넌트가 입력되는 스텝과정
        라이프사이클 훅 -> 컴포넌트가 입혀지는 짧은 순간 과정 사이사이에 명령을 추가하거나 등의 제어를 할수있음.
        훅의 종류는 아래와 같음
        > beforeCreate()
        > created() // HTML 생성 전 데이터만 존재할 시
        > beforeMount{}
        > mounted{}
        > beforeUpdate{}
        > updated{}
        > beforeUnmount{}
        > unmounted{}
        
        *********************************************************************************************************************************
        *********************************************************************************************************************************

        setTimeout(function(){ // function 펑션문
          this.showBanner = false;  
        }, 5000);

        setTimeout(() => { // arrow 펑션문
          this.showBanner = false;  
        }, 5000);

        // this 변수를 가져다 쓸 땐 꼭 펑션문을 '=>' 펑션문으로 바꿔주어야 함
        -->
  </div>
  <!-- // 라이프사이클 메모영역 -->

  <Header />
  <Banner v-if="isBanner" :discountRate="discountRate" btn="구매하러 가기" link="https://www.google.co.kr/" target="_blank"/>
  
  <router-view></router-view>
  
  <Modal v-if="isModal" @popup_close="popup_off()" :viewimg="Modalimg" :viewName="ModalName" :viewPrice="ModalPrice" :viewContent="ModalContent" />
  <p>newjop</p>
</template>

<!-- 스크립트 선언 형식 -->
<!--
data() { return { ... } }
props: { key: Type, ... }
methods: { ... }
watch: { key(newValue, oldValue) { ... } }
-->
<!-- // 스크립트 선언 -->
<script>
import Header from './components/Header.vue';
import Banner from './components/Banner.vue';
import Modal from './components/Modal.vue';

export default {
  name: 'App',
  components: {  
    Header: Header,
    Modal: Modal,
    Banner : Banner
  },
  data() {
    return { 
      /* 배너 */      
      discountRate: 10,
      isBanner: true,
      /* 팝업 */
      isModal: false
    }
  },
  /*
  mounted() {
    const myTimer = setInterval(() => {
      if (this.discountRate > 1) {
        this.discountRate--;
      } else {
        alert("할인 이벤트 종료!");
        clearInterval(myTimer);
        this.isBanner = false;
      }
    }, 1000);    
  }
  */
}
</script>

<style>
  body {margin: 0;}
  div {box-sizing: border-box;}
  img {width: 100%;}
  #app {font-family: Avenir, Helvetica, Arial, sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-align: center;color: #2c3e50;}
  #app img{width:100%;}
  .menu{background: darkslateblue;padding: 15px;border-radius: 5px;}
  .menu a{color: white;padding: 10px;}
</style>
