<template>
  <div :id="roomName">
    <h4>{{ roomtitle }}</h4>
    <img :src="roomImage" alt="">
    <p>{{ roomContent }}</p>
    <p>{{ roomPrice }}</p>
    <button @click="$emit('increase')">허위매물신고</button><br>
    <span class="room_increase">신고 수: {{ roomIncrease }}</span><br>
    <button @click="$emit('popup_open', roomNumber)">상세페이지</button>
  </div>
</template>

<script>
export default {
    name: 'RoomUI',
    props: {
      roomNumber: Number,
      roomName: String,
      roomtitle: String,
      roomImage: String,
      roomContent: String,
      roomPrice: Number,
      roomIncrease: Number
    }
}
</script>

<style>
  .room-img {width: 100%;margin-top: 40px;}
</style>